"use client";
import Image from "next/image";

const Products = [
  {
    title: "Smart watches",
    image: "/watch.png",
    discount: "-25%",
  },
  {
    title: "Laptops",
    image: "/images/laptops.jpg",
    discount: "-15%",
  },
  {
    title: "GoPro cameras",
    image: "/images/cameras.jpg",
    discount: "-40%",
  },
  {
    title: "Headphones",
    image: "/images/headphones.jpg",
    discount: "-25%",
  },
  {
    title: "Canon cameras",
    image: "/images/canon.jpg",
    discount: "-25%",
  },
];

const HomeDecor = () => {
  return (
    <div className="shadow-md rounded-lg p-5">
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h2 className="text-xl font-bold">Deals and offers</h2>
            <p className="text-sm text-gray-500">Hygiene equipment</p>
          </div>
        </div>

        {Products.map((product, index) => (
          <div
            key={index}
            className="flex flex-col items-center text-center border p-4 rounded shadow-sm"
          >
            <Image
              src={product.image}
              alt={product.title}
              width={100}
              height={100}
              className="object-cover rounded"
            />
            <h3 className="mt-2 text-sm font-bold">{product.title}</h3>
            <p className="text-red-500 mt-1">{product.discount}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default HomeDecor;
